
  body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: #f0f0f0;
  }
  
  form {
    display: flex;
    justify-content: center;
    margin: 20px 0;
  }
  
  input {
    padding: 10px;
    width: 300px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    padding: 10px;
    background: #28a745;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background: #218838;
  }
  
  div {
    padding: 10px;
    margin: 0px;
    background: white;
    border-radius: 4px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .spinner {
    text-align: center;
    margin: 20px;
  }
  
  .error {
    color: red;
    text-align: center;
    margin: 20px;
  }
  
  .load-more {
    display: block;
    margin: 20px auto;
    padding: 10px 20px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .load-more:hover {
    background: #0056b3;
  }
      